<template>
  <div id="app">
     
    <div class="column is-half is-offset-one-quarter">
     <img src="../src/assets/pokemons.png" class="bg">
     <hr>
     <h4 class="is-size-4">Pokemons </h4>
     <input type="text" class="input is-rounded" id="" placeholder="buscar pokemons" v-model="busca">
        <button class="button is-danger" id="buscaBtn" @click="buscar" >buscar</button>
        <div v-for= "(poke,index) in filteredPokemons" :key="poke.url">
          <Pokemons :name ="poke.name" :url="poke.url" :num="index+1" />
    </div>  
    <footer class="foot">
      <h4> Desenvolvido por fins educativos por Maxine</h4>
      <h5> Creditos : Curso GuiaDoProgramadorbr </h5>
    </footer> 
    </div> 
  </div>
  
</template>

<script>

import axios from 'axios'
import Pokemons from './components/Pokemons'

export default {
  data() {
    return {
      pokemon : [],
      filteredPokemons : [],
      busca: '',

    }
  },

  name: 'App',
 
 created: function(){
     axios.get('https://pokeapi.co/api/v2/pokemon?limit=152&offset=0').then(res =>{
      this.pokemon = res.data.results;
      this.filteredPokemons = res.data.results;
    }) 
    },
  methods: {
    buscar:function(){
      this.filteredPokemons = this.pokemon
      
      if(this.busca === '' || this.busca ===' '){
      this.filteredPokemons = this.pokemon
       
      }else{
      this.filteredPokemons = this.pokemon.filter(pokemon => 
        pokemon.name == this.busca)
       }


    }
  },
  components:{
    Pokemons
  },
  computed:{
   /* resultBusca:function(){
      if(this.busca =='' || this.busca ==' '){
        return this.pokemon
      }else{
        return this.pokemon.filter(pokemon => 
        pokemon.name == this.busca)
    }
  }  
  */
  }
  

}
</script>

