<template>
    <div id="pokemons">
    <div class="card">
      <div class="card-image">
        <figure class="img is-4by3">
          <img :src="currentImg" alt="placeholder img">
        </figure>
      </div>
      <div class="card-content">
      <div class="media">
      <div class="media-content">
        <p class="title is-4">{{num}}-{{ name | upper}}h</p>
        <p class="subtitle is-6">{{pokemon.type}}</p>
        </div>
        </div>

      <div class="content">
       <button class="button is-mediun is-fullwidth" @click="mudarSprite">Mudar sprite</button> 
      </div>
    </div>
     </div>
    </div>
</template>

<script>

import axios from 'axios';


export default {
    created : function(){
      axios.get(this.url).then(res => {
        this.pokemon.type  = res.data.types[0].type.name;
        this.pokemon.front = res.data.sprites.front_default;
        this.pokemon.back = res.data.sprites.back_default;
        this.currentImg = this.pokemon.front;
        // console.log(this.pokemon.back)
      })
    },

    data(){
      return {
        isFront: true,
        currentImg:'',
         
        pokemon : {
          type:  '',
          front: '',
          back: ''
        }
      }
    },
    props:{
        num: Number,
        name:String,
        url :String,
    },
    
    filters:{
    upper: function(value) {
      var newname = value[0].toUpperCase() + value.slice(1);
      return newname;
      }
    },
    methods: {
      mudarSprite : function(){
      
      if(this.isFront){
        this.isFront = false;
        this.currentImg = this.pokemon.back;
        //console.log('mudei o mambo')
      }else{
        this.isFront = true;
        this.currentImg = this.pokemon.front;
        //console.log('mudei o mambo dois yh')
        }
      }
    }
}
</script>

<style scoped>
  #pokemons{
    margin-top:0.8rem;
  }
</style>